export default {
	data() {
		return {
			checked: false,
			checkeds: false,
			student: {
				stu_num: "",
				stu_name: ""
			},
			items: [{
					value: '良好',
					name: '良好'
				},
				{
					value: '咳嗽',
					name: '咳嗽'
				},
				{
					value: '乏力',
					name: '乏力'
				},
				{
					value: '发热',
					name: '发热'
				}
			],
		}
	},
	onLoad: function() {
		if (uni.getStorageSync('token')) {
			this.student.stu_name = uni.getStorageSync("stu_name")
			this.student.stu_num = uni.getStorageSync("stu_num")
		} else {
			uni.navigateTo({
				url: '../login/login'
			})
		}
	},
	methods: {
		formSubmit: function(e) {
			//major_id需要动态读取
			var data = {
				stu_id: this.student.stu_num,
				major_id: uni.getStorageSync("major_id"),
				create_time: Date.now()
			}
			var formData = e.detail.value,
				data;
			formData.health = formData.health.join();
			var submitData = Object.assign(formData, data);

			console.log(submitData);
			uni.showLoading({
				title: '正在提交，请稍后...'
			})
			uniCloud.callFunction({
				name: 'studentDay',
				data: submitData
			}).then((res) => {
				uni.hideLoading()
				// 添加成功返回ID
				if (res) {
					uni.showModal({
						title: '提示',
						content: '提交成功！',
						showCancel: false,
						success: (res) => {
							uni.navigateTo({
								url: "../index/index"
							})
						}
					})
				}else{
					uni.showModal({
						title: '提示',
						content: '提交失败！请通知管理员',
						showCancel: false,
						
					})
				}
			}).catch((err) => {
				uni.hideLoading()
				uni.showModal({
					content: `提交数据失败，错误信息为：${err.message}`,
					showCancel: false
				})
				console.error(err)
			})
			console.log(submitData)
		},
		checkboxChange: function(e) {
			var items = this.items,
				values = e.detail.value;
			for (var i = 1, lenI = items.length; i < lenI; ++i) {
				const item = items[i]
				if (values.indexOf(item.value) >= 0) {
					this.$set(item, 'checked', true)
				} else {
					this.$set(item, 'checked', false)
					this.$set(item, 'disabled', false)
				}
				if (values.indexOf(items[0].value) >= 0) {
					this.$set(item, 'checked', false)
					this.$set(item, 'disabled', true)
				}
			}
		}
	}
}
